import apiCore from "@/utils/apiCore";


const HTTP = new apiCore({
    baseURL: process.env.VUE_APP_URL,
    headers: {
        'Content-Type': 'application/json;charset=utf-8',
    },
});

export function getOrgTree (id:any) {
    return HTTP.get('/sys/organization/tree/'+id, {});
}

export function addObj(data:any) {
    return HTTP.post('/sys/organization', data);
}

export function putObj(data:any) {
    return HTTP.post('/sys/organization/updateInfo', data);
}

export function delObj(id:any) {
    return HTTP.delete('/sys/organization/' + id, {});
}
