import apiCore from "@/utils/apiCore";


const HTTP = new apiCore({
    baseURL: process.env.VUE_APP_URL,
    headers: {
        'Content-Type': 'application/json;charset=utf-8',
    },
});

export function getSelectData() {
    return HTTP.get('/sysrole/select', {});
}


export function getPage (query:any) {
    return HTTP.get('/sysrole/page', query);
}

export function getObj (id:any) {
    return HTTP.get('/sysrole/' + id, {});
}

export function addObj (obj:any) {
    return HTTP.post('/sysrole/', obj);
}

export function putObj (obj:any) {
    return HTTP.post('/sysrole/updateInfo', obj);
}

export function delObj (id:any) {
    return HTTP.delete('/sysrole/' + id, {});
}

export function getPermissionCode (roleCode:any) {
    return HTTP.get('/sysrole/permission/code/' + roleCode, {});
}
