








































































































































import {Component, Mixins} from 'vue-property-decorator';
import {getPage, delObj, putUserScope} from '@/api/manage/sys/sysUserApi';
import {tablePageMixin} from '@/mixins/tablePageMixin';
import SysUserForm from '@/views/manage/sys/sysuser/SysUserForm.vue';
import {getSelectData} from '@/api/manage/sys/roleApi';
import {getUserScope} from '@/api/manage/sys/sysUserApi';
import {getOrgTree} from '@/api/manage/sys/organizationApi';
import ContentFooter from '@/components/ContentFooter.vue';

@Component({
  components: {
    SysUserForm
  }
})
export default class SysUserPage extends Mixins(tablePageMixin) {
  getPage = getPage;
  delObj = delObj;
  pageTitle = '系统用户';
  rowKey = 'userId';

  delPopVisible: number = 0;
  scopePopVisible: number = 0;
  roleSelectData: any = [];
  roleCodes: any = [];
  userId: number = 0;
  orgTreeData: any = [];

  defaultProps: any = {
    children: 'children',
    label: 'name'
  };

  mounted() {
    console.log(this.orgInfo);
    this.queryParam.organizationId = this.orgInfo.id;
    let _this = this;
    getSelectData().then((res: any) => {
      _this.roleSelectData = res.data;
    });
    getOrgTree(this.userInfo.organizationId).then((res: any) => {
      _this.orgTreeData = res.data;
    });
    this.formPage = this.$refs.formPage;
  }

  handleUserScope(record: any) {
    this.userId = record.userId;
    getUserScope(this.userId)
        .then((res: any) => {
          if (res.data.roleCodes) {
            this.roleCodes = res.data.roleCodes.map(String);
          }
        });
    this.scopePopVisible = this.userId;
  }


  handleUserScopeOk() {
    const userScope = {
      roleCodes: this.roleCodes
    };
    putUserScope(this.userId, userScope).then((res: any) => {
      if (res.code === 200) {
        this.$message.success(res.msg);
        this.scopePopVisible = 0;
      } else {
        this.$message.warning(res.msg);
      }
    });
  }

  handleNodeClick(data: any) {
    this.queryParam.hierarchy = data.hierarchy;

    this.loadData();
  }

}
